import React from 'react';
import { Link } from "react-router-dom";
export class Menu extends React.Component {
    render() {
        return (
            <>
                <div>
                    <nav className="navbar navbar-expand-md bg-dark navbar-dark">
                        <span className="navbar-brand" style={{ color: 'red' }}><Link className="navbar-nav" to="/home" style={{color:'red', textDecoration:'none' }}>MovieFlix</Link></span>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="collapsibleNavbar">
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <span className="nav-link" ><Link className="navbar-nav" to="/homepage" style={{ textDecoration:'none' }}>Home</Link></span>
                                </li>
                                <li className="nav-item">
                                    <span className="nav-link" ><Link className="navbar-nav" to="/addmovie" style={{textDecoration:'none'}}>Upload Movie</Link></span>
                                </li>
                            </ul>
                        </div>

                    </nav>
                </div>
            </>
        )
    }

}